<template>
  <header class="navbar">
    <Row>
      <Col span="24">
        <Form>
          <Form-item>
            <h5>{{textDic.title}}</h5>
          </Form-item>
          <Form-item>
            <div style="width: 1200px; height: 1px; background-color: #333;margin: 20px 0;"></div>
          </Form-item>
        </Form>
      </Col>
    </Row>
    <Row>
      <Col span="24">
      <header class="navbar">
        <span>{{textDic.text0}}</span>
        <Date-picker type="date"
                @on-change="handleChangeFrom"
                format="yyyy年MM月dd日" 
                :placeholder="textDic.text1" 
                style="width: 200px"></Date-picker>
        <span>{{textDic.text2}}</span>
        <Date-picker type="date"
                v-model="value1"
                @on-change="handleChangeEnd"
                format="yyyy年MM月dd日" 
                :placeholder="textDic.text3"
                style="width: 200px"></Date-picker>
        <span style="margin-left: 5%">{{textDic.text4}}</span>
        <select v-model="formData.value2" style="width:200px">
          <option :value="textDic.text6">{{textDic.text6}}</option>
          <option :value="textDic.text7">{{textDic.text7}}</option>
        </select>
        <span style="margin-left: 5%">{{textDic.text8}}</span>
        <Input v-model="formData.value3" :placeholder="textDic.text8" style="width: 200px"/>
        <Button type="primary">{{textDic.text19[0]}}</Button>
        <Button type="primary">{{textDic.text19[1]}}</Button>
      </header>
      </Col>
    </Row>
    <Row>
      <Col span="24">
        <header class="navbar addBtn">
          <Button type="primary" @click="didClickAdd">{{textDic.text9}}</Button>
        </header>
        <table border="1px" cellspacing="0" cellpadding="0">
          <tr>
            <th v-for="(title, i) in textDic.tableTitles" :key="i">{{title}}</th>
          </tr>
          <tr v-for="(formItem, index) in formItems" :key="index">
            <td v-for="(text, j) in formItem" :key="j">
              <custom-cell :index="index" :cellData="cellText(j, text)" :type="cellType(j)" @didClickButton="didClickButton"></custom-cell>
            </td>
          </tr>
        </table>
      </Col>
    </Row>
    <Modal
        v-model="modal0"
        :title="textDic.text12"
        @on-ok="didClickModel0(true)"
        @on-cancel="didClickModel0(false)">
        <p>{{textDic.text13}}</p>
    </Modal>
    <Modal
        v-model="modal1"
        :title="textDic.text14"
        @on-ok="didClickModel1(true)"
        @on-cancel="didClickModel1(false)">
      <Form :label-width="80">
        <Form-item v-for="(text, index) in textDic.editTitles" :key="index" :label="text">
            <Input v-model="editForm[index]"/>
        </Form-item>
        <Form-item :label="textDic.text15">
          <select v-model="editForm[3]">
            <option :value="textDic.text17[0]">{{textDic.text17[0]}}</option>
            <option :value="textDic.text17[1]">{{textDic.text17[1]}}</option>
          </select>
        </Form-item>
        <Form-item :label="textDic.text16">
          <Radio-group v-model="editForm[4]" v-for="(text, index) in textDic.text18" :key="index">
            <Radio :label="textDic.text18[index]">{{textDic.text18[index]}}</Radio>
          </Radio-group>
        </Form-item>
      </Form>
    </Modal>
  </header> 
</template>

<script>
import CustomCell from '../components/CustomCell'
export default {
  components: {
    CustomCell
  },
  data() {
    return {
      formData: {
        value0: '', // 开始日期
        value1: '', // 结束日期
        value2: '', // 状态
        value3: '', // 手机号
      },
      formItems: [
        [1546247235000, 0, 13777777777, 1, ''],
        [1546247235000, 0, 13777777777, 1, ''],
      ],
      modal0: false,
      modal1: false,
      currentIndex: 0, // 当前删除或者编辑的索引
      editForm: ['', '', '', '', ''],
    }
  },
  methods: {
    didClickButton(data) {
      this.currentIndex = data.index; // 哪一行
      if(data.i ==0) {
        // 点击了删除
        this.modal0 = true;
      }else {
        // 点击了编辑
        this.modal1 = true;
      }
    },
    cellText(j, text) {
      return j==4 ? [this.textDic.text10, this.textDic.text11] : [text]
    },
    cellType(j) {
      if(j == 0) {
        return 'date';
      }else if(j == 4) {
        return 'button';
      }else {
        return 'text';
      }
    },
    tableText(i, j, text) {
      // 如果是第一行，说明是标题，直接返回
      if(i == 0) {
        return text;
      }
      /**
       * 第一行是日期
       * 第二行是账号类型
       * 第三行是手机号
       * 第四行是状态
       * 第五行是操作
       * */
      switch(j){
        case 0:
          return text;
          break;
        case 1:
          return text;
          break;
        case 2: 
          return text;
          break;
        case 3:
          return text;
          break;
        default:
        return text;
      }
    },
    handleChangeFrom(date) {
      this.formData.value0 = date
    },
    handleChangeEnd(date) {
      this.formData.value1 = date
    },
    didClickAdd() {

    },
    didClickModel0(isEdit) {
      // this.currentIndex的删除最终是否确认
    },
    didClickModel1(isEdit) {
      // this.currentIndex的编辑最终是否确认
    }
  },
  computed:{
    textDic() {
      return {
        title: '账户管理',
        text0: '添加日期',
        text1: '开始日期',
        text2: '---',
        text3: '结束日期',
        text4: '状态',
        text5: '请选择',
        text6: '正常',
        text7: '停用',
        text8: '手机号',
        text9: '添加',
        tableTitles: ['添加日期', '账号类型', '手机号', '状态', '操作'],
        text10: '删除',
        text11: '编辑',
        text12: '提示',
        text13: '您确定要删除此账号吗?',
        text14: '编辑',
        editTitles: ['手机号*', '真实姓名*', '密码*'],
        text15: '角色选择',
        text16: '账号状态',
        text17: ['超级管理员', '客服'],
        text18: ['停用', '启用'],
        text19: ['搜索', '重置']
      }
    }
  }
}
</script>

<style scoped>
  .addBtn {
    background-color: #333;
    margin-top: 60px;
    padding: 5px 10px;;
  }
  td, th {
    width: 218px; 
    height: 60px;
  }
</style>



