<template>
  <div class="wrapper">
    <div v-for="(text, i) in cellData" :key="i">
      <span v-if="type != 'button'" class="">{{cellText(text)}}</span>
      <Button v-if="type == 'button'" type="primary" @click="didClickButton(i)">{{text}}</Button>
    </div>
  </div>
</template>

<script>

export default {
  props: {
    cellData: {
      type: Array,
      required: false,
      default: []
    },
    type: {
      type: String,
      required: false,
      default: "text" // text是文本, button是按钮, date是日期
    },
    index: {
      type: Number,
      required: false,
      default: 0
    }
  },
  methods: {
    didClickButton(i) {
      this.$emit("didClickButton", {index: this.index, i:i});
    },
    cellText(text) {
      if(this.type == 'date') {
        return this.timetrans(text);
      }else {
        return text;
      }
    },
    timetrans(date){
      var date = new Date(date);//如果date为13位不需要乘1000
      var Y = date.getFullYear() + '-';
      var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
      var D = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate()) + ' ';
      return Y+M+D;
}

  }
}
</script>

<style scoped>
.wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
</style>



